.icon-wrap {
  cursor: pointer;
  position: absolute;
  display: block;
  width: 30px;
  height: 30px;
  left: 10%;
  top: calc(50% - 30px); }
  .icon-wrap-next {
    right: 10%;
    transform: rotate(180deg);
    cursor: pointer;
    position: absolute;
    display: block;
    width: 30px;
    height: 30px;
    top: calc(50% - 30px); }
    .icon-wrap-next:before {
      -webkit-transform: translateX(-50%) rotate(30deg);
      transform: translateX(-50%) rotate(30deg);
      -webkit-transform-origin: 0 100%;
      transform-origin: 0 100%;
      content: " ";
      position: absolute;
      left: 25%;
      width: 3px;
      height: 50%;
      background: #fff;
      -webkit-transition: -webkit-transform .3s,background-color .3s;
      transition: transform .3s,background-color .3s;
      -webkit-backface-visibility: hidden;
      backface-visibility: hidden; }
    .icon-wrap-next:after {
      top: 50%;
      -webkit-transform: translateX(-50%) rotate(-30deg);
      transform: translateX(-50%) rotate(-30deg);
      -webkit-transform-origin: 0 0;
      transform-origin: 0 0;
      content: " ";
      position: absolute;
      left: 25%;
      width: 3px;
      height: 50%;
      background: #fff;
      -webkit-transition: -webkit-transform .3s,background-color .3s;
      transition: transform .3s,background-color .3s;
      -webkit-backface-visibility: hidden;
      backface-visibility: hidden; }
